.footer {
  position: relative;
  padding: 40px 0;
  overflow: hidden;
  @include media-breakpoint-down(lg) {
    padding: 80px 0 35px;
  }
  @include media-breakpoint-down(md) {
    padding: 40px 0;
    border-top: 1px solid #ccc;
  }
  .container {
    display: flex;
    @include media-breakpoint-down(md) {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  &__contacts {
    margin-right: 100px;
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    flex-shrink: 0;
    @include media-breakpoint-down(md) {
      margin-right: 0;
      align-items: center;
      margin-bottom: 15px;
    }
  }
  &__title {
    display: none;
    font-size: 30px;
    @include media-breakpoint-down(md) {
      display: block;
      font-family: "TTNormsExtraBold";
    }
  }
  &__address {
    margin-bottom: 15px;
    display: none;
    font-size: 16px;
    @include media-breakpoint-down(md) {

    }
  }
  &__logo {
    margin-bottom: 20px;
    width: 153px;
    height: 102px;
    object-fit: contain;
  }
  &__phone {
    font-family: 'TTNormsExtraBold', sans-serif;
    letter-spacing: 1.2px;
    font-size: 24px;
    @include media-breakpoint-down(md) {
      font-size: 16px;
      line-height: 1.2;
    }
  }
  &__email {
    margin-bottom: 10px;
    font-size: 18px;
    @include media-breakpoint-down(md) {
      margin: 15px 0;
      font-size: 16px;
    }
  }
  &__messanger {
    display: flex;
    width: 100%;
    align-items: center;
    margin-top: auto;
    font-family: 'TTNormsMedium', serif;
    color: #64b161;
    &:visited {
      color: #64b161;
    }
    @include media-breakpoint-down(md) {
      justify-content: center;
      margin-bottom: 20px;
    }
    img {
      margin-right: 10px;
      flex-shrink: 0;
      height: 43px;
      object-fit: contain;
    }
  }

  &__wrap {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 800px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
  &__nav {
    margin-bottom: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 800px) {
      display: none;
    }
    a {
      font-size: 16px;
      color: #262626;
      letter-spacing: 3.2px;
      transition: 200ms ease;
      &:hover {
        color: #04aaca;
        transition: 200ms ease;
      }
    }
  }
  &__items {
    margin: 0;
    margin-top: auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    @media (max-width: 800px) {
      display: none;
    }
    li {
      position: relative;
      display: flex;
      align-items: center;
      &:not(:last-of-type) {
        margin-bottom: 10px;
      }
      &:before {
        content: '';
        display: block;
        margin-right: 5px;
        height: 3px;
        width: 3px;
        border-radius: 50%;
        background-color: #262626;
        opacity: 0.75;
      }
    }
    a {
      font-size: 14px;
      opacity: 0.75;
      letter-spacing: 0.7px;
      color: #262626;
      font-family: "TTNormsMedium", sans-serif;
      &:hover{
        color: #04aaca;
        transition: 200ms ease;
      }
    }
  }
  &__cards {
    align-self: flex-end;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 150px;
    @media (max-width: 800px) {
      align-self: center;
      margin-bottom: 15px;
      justify-content: center;
      order: 1;
    }
    &_title {
      font-family: 'TTNormsBold', serif;
      font-size: 16px;
      width: 100%;
      margin-bottom: 15px;
    }
    img {
      height: 34px;
      width: 54px;
      transform: scale(1.6);
      &:not(:last-of-type) {
        margin-right: 5px;
      }
    }
  }
  &__social {
    position: relative;
    display: flex;
    flex-direction: column;
    &_wrap {
      margin-top: auto;
      @media (max-width: 800px) {
        margin-bottom: 20px;
      }
    }
    &_title {
      font-family: "TTNormsExtraBold";
      font-size: 16px;
      width: 100%;
      margin-bottom: 15px;
      @media (max-width: 800px) {
        max-width: 200px;
        margin: 0 auto 15px;
      }
    }
    img {
      height: 29px;
      width: 29px;
      object-fit: contain;
      &:not(:last-of-type) {
        margin-right: 10px;
      }
    }
  }
  &__performer{
    display: flex;
    align-items: center;
    margin-top: rem(50);
    span{
      font-family: "TTNormsExtraBold", sans-serif;
      letter-spacing: 1.2px;
      font-size: rem(16);
      margin-right: rem(15);
    }
    img{
      width: rem(88);
      height: rem(59);
      object-fit: contain;
    }
  }
}
